





































import { Vue, Component } from 'vue-property-decorator'
import { adminGetMessage, adminUpdateMessage } from 'utils/api'

@Component({})
export default class ViewMessage extends Vue {
  messages: any = []
  selections: any = []
  editForm: any = {
    index: '',
    title: '',
    content: '',
    releasedTime: ''
  }
  editFormRules: any = {
    title: [
      { required: true, message: '请填写标题', trigger: 'blur' }
    ],
    content: [
      { required: true, message: '请填写内容', trigger: 'blur' }
    ]
  }
  editFormVisible: boolean = false

  mounted () {
    adminGetMessage()
      .then((res) => {
        if (res.data) {
          res.data.forEach((datum) => {
            datum.releasedTime = datum.releasedTime.split('T')[0]
            this.messages.push(datum)
          })
        } else {
          this.$message({
            message: '读取失败',
            type: 'error'
          })
        }
      })
      .catch((err) => {
        this.$message({
          message: err,
          type: 'error'
        })
      })
  }

  handleSelectionChange (val) {
    this.selections = val
  }

  editMessage (index, row) {
    this.editFormVisible = true
    this.editForm = {
      index: index,
      title: row.title,
      content: row.content,
      releasedTime: row.releasedTime
    }
  }

  deleteMessage (index) {
    this.$confirm('确认删除？')
      .then(() => {
        this.messages.splice(index, 1)
        adminUpdateMessage(this.messages)
          .then((res) => {
            if (res.data) {
              this.$message({
                message: '删除成功',
                type: 'success'
              })
            } else {
              this.$message({
                message: '删除失败',
                type: 'error'
              })
            }
          })
          .catch((err) => {
            this.$message({
              message: err,
              type: 'error'
            })
          })
      })
      .catch(() => {
      })
  }

  editSubmit () {
    this.messages[this.editForm.index].title = this.editForm.title
    this.messages[this.editForm.index].content = this.editForm.content
    adminUpdateMessage({
      messages: this.messages
    }).then((res) => {
      if (res.data.succeed) {
        this.$message({
          message: '编辑成功',
          type: 'success'
        })
      } else {
        this.$message({
          message: res.data.msg,
          type: 'error'
        })
      }
    }).catch((err) => {
      this.$message({
        message: err,
        type: 'error'
      })
    })
  }

  handleEditClose () {
    this.$confirm('确认关闭？')
      .then(() => {
        this.editFormVisible = false
      })
      .catch(() => {
      })
  }
}



